import PropTypes from 'prop-types';
import { useState } from 'react';
import { alpha } from '@mui/material/styles';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
// import DialogContentText from '@mui/material/DialogContentText';
import TextField from '@mui/material/TextField';
import Authenticate from 'hocs/require-authentication';

export default function SaveDialog({ open, onClick, onClose }) {
  const [name, setName] = useState('');
  const [nameError] = useState('');
  const handleSave = () => {
    onClick(name);
  };

  const handleNameChange = (e) => {
    const { target } = e;
    setName(target.value);
  };
  const handleClose = () => {
    setName('');
    onClose();
  };

  return (
    <Authenticate>
      <Dialog open={open} fullWidth>
        <DialogTitle sx={{ textAlign: 'center' }}>Save Collage</DialogTitle>
        <DialogContent
          dividers
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
          }}
        >
          <TextField
            variant="outlined"
            label="Set Collage Name"
            defaultValue={name}
            onChange={handleNameChange}
            size="small"
            sx={{ width: '100%', mb: 1 }}
            InputProps={{ sx: { backgroundColor: alpha('#fff', 0.9) } }}
            helperText={nameError}
            error={!!nameError}
          />
        </DialogContent>
        <DialogActions>
          <Button
            variant="outlined"
            disabled={name.length === 0}
            onClick={handleSave}
          >
            Save
          </Button>
          <Button variant="text" onClick={handleClose}>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </Authenticate>
  );
}

SaveDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onClick: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
};
